import { configureStore } from "@reduxjs/toolkit";
import { ingredientsApi } from "./api/ingredients/ingredients.api";
import { orderBurgerApi } from "./api/order-burger/order-burger";
import burgerReducer from "./burger/burger";
import currentIngredientReducer from "./current-ingredient/current-ingredient";
import orderNumberReducer from "./order-number/order-number";

export const store = configureStore({
  reducer: {
    [ingredientsApi.reducerPath]: ingredientsApi.reducer,
    [orderBurgerApi.reducerPath]: orderBurgerApi.reducer,
    currentIngredient: currentIngredientReducer,
    burger: burgerReducer,
    orderNumber: orderNumberReducer,
  },
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware().concat(
      ingredientsApi.middleware,
      orderBurgerApi.middleware
    ),
});
