import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";

export const orderBurgerApi = createApi({
  reducerPath: "api/orderBurger",
  baseQuery: fetchBaseQuery({
    baseUrl: "https://norma.nomoreparties.space/api",
  }),
  endpoints: (builder) => ({
    orderBurger: builder.mutation({
      query: (payload) => ({
        url:`/orders`,
        method: 'POST',
        headers: {
          'Content-type': 'application/json; charset=UTF-8',
        },
        body: {ingredients: payload},
      }),
    }),
  }),
});

export const { useOrderBurgerMutation } = orderBurgerApi;

export const orderBurgerResult = orderBurgerApi.endpoints.orderBurger.select()
